body {
  background-color: #111;
}

.search-catalog {
  border-radius: 30px;
  justify-content: center;
  gap: 5px;
  margin: 80px 0 48px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.gallery img {
  height: 574px;
  width: 395px;
  background-color: linear-gradient(180deg, rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  border-radius: 5px;
}

.movie {
  position: relative;
}

.movie-title {
  color: #fff;
  position: absolute;
  bottom: 20px;
  left: 20px;
  right: 0;
}

.pagination {
  justify-content: center;
  gap: 5px;
  margin: 10px;
  display: flex;
}

.search {
  height: 48px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fff;
  border-radius: 8px;
}

.search-btn {
  height: 48px;
  width: 48px;
  background-color: var(--orange-color);
  color: #fff;
  border: none;
  border-radius: 50%;
}

.form {
  border: 1px 0px 0px 0px;
  opacity: 0px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px 0 0;
  gap: 10px;
}

.pagination-btn {
  width: 40px;
  height: 40px;
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #b7b7b7;
  border-radius: 50%;
}

.pagination-btn.active {
  background-color: var(--orange-color);
  color: #fff;
  border: none;
}

.prevnext-btn {
  color: #fff;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.dots {
  color: #fff;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu {
  background-color: var(--black-color);
  height: 60px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.nav {
  flex-grow: 1;
  justify-content: space-between;
  align-items: center;
  padding: 0 32px;
  display: flex;
}

.nav-list {
  flex-flow: row;
  gap: 40px;
  margin: 0 auto;
  padding: 8px 0;
  display: flex;
}

.cinemania {
  color: var(--white-color);
  text-align: center;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 400;
  line-height: 28.13px;
}

.logo {
  z-index: 5;
  align-items: center;
  gap: 8px;
  display: flex;
}

.navigation {
  color: var(--grey-color);
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 500;
}

.navigation:active {
  color: var(--orange-color);
}

.nav-mobile {
  width: 204px;
  height: 100%;
  background-color: var(--black-color);
  border-radius: 0 8px 8px 0;
  transition-duration: .5s;
  position: absolute;
  top: 0;
  transform: translate(-100%);
}

.nav-list-mobile {
  flex-direction: column;
  gap: 24px;
  margin-top: 88px;
  margin-left: 20px;
  display: flex;
}

.text-mobile {
  color: var(--white-color);
  font-weight: 400;
}

.navigation-activ {
  color: var(--orange-color);
}

.button-menu {
  text-align: center;
  color: var(--grey-color);
  cursor: pointer;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  display: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, .2);
  transition-duration: .5s;
  position: fixed;
  top: 0;
  transform: translate(-100%);
}

.hidden {
  transform: translate(0%);
}

@media screen and (max-width: 767px) {
  .button-menu, .nav-mobile {
    display: flex;
  }

  .cinemania, .nav-list {
    display: none;
  }

  .mobile-menu {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .menu {
    height: 48px;
  }

  .logo {
    width: 46px;
  }
}

.hero.container {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(83deg, #111 36.85%, rgba(17, 17, 17, 0) 60.05%), url("../Hero_mobile.7d8ac122.png");
  background-position: 100%;
  background-repeat: repeat, no-repeat;
  background-size: contain;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  font-family: Roboto, sans-serif;
}

.intro {
  color: #fff;
  max-width: 232px;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 20px;
  padding-top: 40px;
  padding-left: 20px;
  display: flex;
}

.title {
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
}

.text {
  max-width: 177px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  overflow: hidden;
}

.buttons {
  gap: 8px;
  font-family: inherit;
  display: flex;
}

.hero-btn.cta {
  color: var(--black-color, #111);
  text-align: center;
  cursor: pointer;
  background: linear-gradient(141deg, #ffc226 9.4%, #f84119 91.91%);
  border: none;
  border-radius: 74px;
  align-items: center;
  gap: 10px;
  margin-left: 20px;
  padding: 12px 24px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  display: inline-flex;
}

.hero-btn.more-details {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background: none;
  border: 1px solid #fff;
  border-radius: 74px;
  align-items: flex-start;
  gap: 10px;
  margin-left: 8px;
  padding: 12px 24px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  display: inline-flex;
}

@media (min-width: 768px) {
  .hero.container {
    height: 372px;
    background-color: rgba(0, 0, 0, 0);
    background-image: linear-gradient(83deg, #111 36.85%, rgba(17, 17, 17, 0) 60.05%), url("../Hero_tablet.99cc7617.png");
    background-position: 100%;
    background-repeat: repeat, no-repeat;
    background-size: contain;
    background-attachment: scroll, scroll;
    background-origin: padding-box, padding-box;
    background-clip: border-box, border-box;
  }

  .intro {
    max-width: 445px;
    margin-bottom: 32px;
    padding-top: 62px;
    padding-left: 32px;
  }

  .hero-btn {
    height: 40px;
    font-size: 14px;
  }

  .hero-btn.cta {
    margin-left: 32px;
  }

  .title {
    font-size: 44px;
  }

  .text {
    max-width: 254px;
    height: 49px;
  }
}

@media (min-width: 768px) and (max-width: 1279px) {
  .hero-btn {
    padding: 10px 24px;
  }
}

@media (min-width: 1280px) {
  .hero.container {
    height: 660px;
    background-color: rgba(0, 0, 0, 0);
    background-image: linear-gradient(83deg, #111 36.85%, rgba(17, 17, 17, 0) 60.05%), url("../Hero_desktop.57ce28b5.png");
    background-position: 100%;
    background-repeat: repeat, no-repeat;
    background-size: contain;
    background-attachment: scroll, scroll;
    background-origin: padding-box, padding-box;
    background-clip: border-box, border-box;
  }

  .intro {
    max-width: 670px;
    gap: 16px;
    margin-left: 135px;
    padding-top: 118px;
  }

  .buttons {
    margin-left: 135px;
  }

  .hero-btn {
    height: 48px;
    font-size: 16px;
  }

  .title {
    font-size: 64px;
  }

  .text {
    max-width: 367px;
    height: 59px;
    font-size: 16px;
  }
}

